import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { AuthServices } from 'src/app/services/auth/auth-services';
import { MustMatch } from 'src/app/services/utilities/mustmatch';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {

  constructor(private snackbarService: SnackbarServices, private authServices: AuthServices, private formBuilder: FormBuilder,) { }
  form: FormGroup;
  model: any = {};
  hide = true;
  loading = false;
  submitted = false;
  ngOnInit() {
    this.form = this.formBuilder.group({
      oldpassword: ['', Validators.required],
      newpassword: ['', [Validators.required, Validators.minLength(6)]],
      confirmpassword: ['', Validators.required],
    }, {
      validator: MustMatch('newpassword', 'confirmpassword')
    });
  }
  get f() { return this.form.controls; }
  changePassword(model: any) {
    console.log("fired");
    this.submitted = true;
    if (this.form.invalid) {
      return;
    }

    this.loading = true;
    this.authServices.changePassword(this.f.oldpassword.value, this.f.newpassword.value, this.f.confirmpassword.value)
      .pipe(first())
      .subscribe({
        next: () => {
          this.snackbarService.openSnackBar(" Password Change successful ", 5000, "center", "top", 'accepted');
          this.loading = false;
        },
        error: error => {
          console.log(error)
          this.snackbarService.openSnackBar(error.error.message, 5000, "center", "top", 'warning');
          this.loading = false;
        }
      });
  }

}
