import { Component, OnDestroy, OnInit } from '@angular/core';
import { FileData } from './../../models/file';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import {environment} from 'src/environments/environment.prod'
import { FileDataService } from 'src/app/services/common-services';
@Component({
  selector: 'app-publications',
  templateUrl: './publications.component.html',
  styleUrls: ['./publications.component.scss']
})
export class PublicationsComponent implements OnInit, OnDestroy {
  filedata: FileData;
  file: any;
  getFileUrl = environment.apiUrl+'file';
  getFileDownloadUrl = environment.apiUrl+'file/download/';
  deleteFileUrl = environment.apiUrl+'file/delete/';
  allFileData: any;
  fileDatas: any;
  allFileDownloadData: Observable<Object>;
  currentfileName: string;
  fileSingleDatas: any;
  status: string;
  singleFileData: any;

  constructor(private http: HttpClient, private snackbarService: SnackbarServices, private fileDataServices: FileDataService) { }
  ngOnDestroy() {
    this.fileDataServices.fileData = this.singleFileData;
  }

  ngOnInit() {
    this.getAllFile().subscribe((response) => {
      console.log(response);
      this.fileDatas = response.data;
      console.log(this.fileDatas);
      console.log(this.fileDatas[0]);
    }, err => {
      this.snackbarService.openSnackBar(" Error to get file names", 5000, "center", "top", 'warning');
    });

  }
  getAllFile(): Observable<any> {
    return this.http.get(this.getFileUrl).pipe(data => this.allFileData = data
    )
  }
  getDownloadFile(currentfileName: string): Observable<any> {
    console.log("update", currentfileName)
    return this.http.get(this.getFileDownloadUrl + currentfileName, { responseType: 'blob' }).pipe(data => this.allFileDownloadData = data
    )
  }
  deletePublications(id: string) {
    this.http.delete(this.deleteFileUrl + id)
      .subscribe((data) => {
        this.status = 'Delete successful'
        Swal.fire('File Successfuly Deleted', 'Your file deleted succesfully!', 'success')
        this.ngOnInit();

      }), err => {
        Swal.fire('File Deleted failed', 'Your file deletion failed!', 'error')
        console.log("Error");
      };

    console.log(id)
  }
  isAdmin(): boolean {
    if (this.getCookie('isAdmin') === 'true') return true;
    return false;
  }
  getCookie(name) {
    var pattern = RegExp(name + "=.[^;]*")
    var matched = document.cookie.match(pattern)
    if (matched) {
      var cookie = matched[0].split('=')
      return cookie[1]
    }
    return false
  }
  onFileDownload(fileName: string) {
    this.getDownloadFile(fileName).subscribe((response: Blob) => {
      var file = new Blob([response], { type: 'application/pdf' })
      var fileURL = URL.createObjectURL(file)
      console.log("response", response);
      //this.fileSingleDatas = response.data;
      window.open(fileURL);
      var a = document.createElement('a');
      a.href = fileURL;
      a.target = '_blank';
      a.download = 'bill.pdf';
      document.body.appendChild(a);
      a.click();

    }, err => {
      console.log(err)
      this.snackbarService.openSnackBar(" Error to get file names", 5000, "center", "top", 'warning');
    });;

  }
  updatePublications(fileData: any) {
    this.singleFileData = fileData;
    console.log(fileData)

  }

}
