import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { catchError } from 'rxjs/operators';
import { Photo } from 'src/app/models/photo';
import { User } from 'src/app/models/user';
import { AuthServices } from 'src/app/services/auth/auth-services';
import { UserService } from 'src/app/services/user.service';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';

@Component({
  selector: 'app-people-update',
  templateUrl: './people-update.component.html',
  styleUrls: ['./people-update.component.scss']
})
export class PeopleUpdateComponent implements OnInit {
  user: User;
  form: FormGroup;
  model: any = {};
  isPhotoUploaded: boolean = false;
  Caption = "Image";
  @ViewChild('editForm') editForm: NgForm;


  fileToUpload: File = null;
  uploadedImageUrl: any;
  imageUrl: string;
  photo: Photo;
  constructor(private userService: UserService, private route: ActivatedRoute, private snackbarService: SnackbarServices, private authService: AuthServices) { }

  ngOnInit(): void {
    this.loadUser();
    this.form = new FormGroup({
      name: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),

    });


  }
  public hasError = (controlName: string, errorName: string) => {
    return this.form.controls[controlName].hasError(errorName);
  }
  loadUser() {
    var userId = this.authService.decodedToken?.nameid;
    this.userService.getUser(userId).subscribe((user: User) => {
      this.user = user;
      if (this.user.photos.length <= 0) {
        this.imageUrl = 'https://i.ibb.co/sFJW3K4/default.jpg';
      }
      else {
        this.imageUrl = this.user.photos[this.user.photos.length - 1].url;
      }

    }, err => {
      this.snackbarService.openSnackBar(" Something went wrong in get user", 5000, "center", "top", 'warning');
    });
    console.log(userId)

  }
  updateUser(): void {

    console.log(this.user)
    this.userService.updateUser(this.authService.decodedToken?.nameid, this.user).subscribe(next => {


      this.snackbarService.openSnackBar("Update User Succeeded", 5000, "center", "top", 'accepted');
      console.log(this.user);
      // this.editForm.reset(this.user);
    }, err => {
      this.snackbarService.openSnackBar("Update User failed", 5000, "center", "top", 'warning');
    })

  }
  handleFileInput(file: FileList) {
    this.fileToUpload = file.item(0);

    //Show image preview
    var reader = new FileReader();
    reader.onload = (event: any) => {
      this.imageUrl = event.target.result;
    }
    reader.readAsDataURL(this.fileToUpload);

  }
  onSubmit() {
    console.log(this.fileToUpload);
    var userId = this.authService.decodedToken?.nameid
    this.userService.updatePhoto(userId, this.fileToUpload).subscribe(
      (data: any) => {
        console.log('done', data);
        console.log(this.user);

      }
    );
  }


}
