import { SnackbarServices } from '../../services/utilities/snackbar-services';
import { Component, OnInit, Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthServices } from 'src/app/services/auth/auth-services';

@Injectable({
  providedIn: 'root'
})
export class Blocker implements OnInit,CanActivate {

  constructor(public authService:AuthServices,private router:Router,private snackbarService:SnackbarServices) { }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    if(this.authService.isLoggedIn()){
    return true;
  }
  this.snackbarService.openSnackBar("Please Login First ",5000,"center","top",'warning');
  this.router.navigate(['/home']);
  return false;
   }

  ngOnInit(): void {
  }

}
