import { HttpClient } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/internal/Observable';
import { FileDataService } from 'src/app/services/common-services';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-update-publications',
  templateUrl: './update-publications.component.html',
  styleUrls: ['./update-publications.component.scss']
})
export class UpdatePublicationsComponent implements OnInit {
  sub: any;
  id: any;
  fileData: any;
  state$: Observable<object>;
  form: FormGroup;
  updateFileUrl = 'http://localhost:5000/api/file/update';
  @ViewChild('editForm') editForm: NgForm;
  status: string;
  file: File;

  constructor(private route: ActivatedRoute, private fileDataServices: FileDataService, private http: HttpClient, private snackbarService: SnackbarServices,) { }

  ngOnInit() {
    this.form = new FormGroup({
      paperTitle: new FormControl('', [Validators.required]),
      authorName: new FormControl('', [Validators.required]),
      details: new FormControl('', [Validators.required]),

    });
    this.sub = this.route.params.subscribe(params => {
      this.id = params['id'];
      this.fileData = this.fileDataServices.fileData;

    });
    console.log(this.id);
    console.log(this.fileData);
  }
  updatePublications(fileData: any) {

    let formData: FormData = new FormData();
    formData.append('PaperTitle', this.fileData.paperTitle + '');
    formData.append('authorName', this.fileData.authorName + '');
    formData.append('details', this.fileData.details + '');
    formData.append('id', this.fileData.id + '');
    // formData.append('source', this.fileData.source + '');
    //formData.append('location', this.fileData.location + '');
    // formData.append('date', this.fileData.date + '');
    //formData.append('file', this.file[0], this.file[0].name);
    this.http.put<any>(this.updateFileUrl, formData)
      .subscribe((data) => {
        this.status = 'Update file information successful'
        Swal.fire('File Information Successfuly Updated', 'Your file updated succesfully!', 'success')

      }), err => {
        Swal.fire('File Update failed', 'Your file update failed!', 'error')
        console.log("Error");
      };

  }

}
