import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/models/user';
import { UserService } from 'src/app/services/user.service';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';

@Component({
  selector: 'app-people',
  templateUrl: './people.component.html',
  styleUrls: ['./people.component.scss']
})
export class PeopleComponent implements OnInit {
  users: User[];
  constructor(private userService: UserService, private snackbarService: SnackbarServices) { }

  ngOnInit(): void {
    this.loadUsers();
  }
  loadUsers() {
    this.userService.getUsers().subscribe((users: User[]) => {
      this.users = users;
      console.log("Users", this.users)

    }, error => {
      this.snackbarService.openSnackBar("User Fetch error occured ", 5000, "center", "top", 'warning');

    })
  }
}
