import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { User } from 'src/app/models/user';
import { UserService } from 'src/app/services/user.service';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';

@Component({
  selector: 'app-people-details',
  templateUrl: './people-details.component.html',
  styleUrls: ['./people-details.component.scss']
})
export class PeopleDetailsComponent implements OnInit {
  user: User;

  constructor(private userService: UserService, private snackbarService: SnackbarServices, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.loadUser();
  }
  loadUser() {
    this.userService.getUser(+this.route.snapshot.params['id']).subscribe((user: User) => {
      this.user = user;
      console.log(this.user)
    }, err => {
      this.snackbarService.openSnackBar(" Something went wrong in get user", 5000, "center", "top", 'warning');
    });
  }

}
