import { Injectable } from '@angular/core';
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';
@Injectable({
  providedIn: 'root'
})
export class SnackbarServices {

  constructor(private snackBar: MatSnackBar) { }

  openSnackBar(errorMessage:string,duration:number,horizontalPosition:any,verticalPosition: any,backgroundpanelClass:string) {
    this.snackBar.open(errorMessage, 'End now', {
      duration: duration,
      horizontalPosition: horizontalPosition,
      verticalPosition: verticalPosition,
      panelClass:[backgroundpanelClass]
    });
  }
}
