import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';
import { AuthServices } from 'src/app/services/auth/auth-services';
import { MustMatch } from 'src/app/services/utilities/mustmatch';
enum TokenStatus {
  Validating,
  Valid,
  Invalid
}
@Component({
  selector: 'app-set-reset-password',
  templateUrl: './set-reset-password.component.html',
  styleUrls: ['./set-reset-password.component.scss']
})

export class SetResetPasswordComponent implements OnInit {

  TokenStatus = TokenStatus;
  tokenStatus = TokenStatus.Validating;
  token = null;
  form: FormGroup;
  loading = false;
  submitted = false;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private authServices: AuthServices,
    private snackbarService: SnackbarServices
  ) { }

  ngOnInit() {
    this.form = this.formBuilder.group({
      password: ['', [Validators.required, Validators.minLength(6)]],
      confirmPassword: ['', Validators.required],
    }, {
      validator: MustMatch('password', 'confirmPassword')
    });

    const token = this.route.snapshot.queryParams['token'];
    this.token = token;

    // remove token from url to prevent http referer leakage
    this.router.navigate([], { relativeTo: this.route, replaceUrl: true });

    // this.authServices.validateResetToken(token)
    //   .pipe(first())
    //   .subscribe({
    //     next: () => {
    //       this.token = token;
    //       this.tokenStatus = TokenStatus.Valid;
    //     },
    //     error: () => {
    //       this.tokenStatus = TokenStatus.Invalid;
    //     }
    //   });
  }
  get f() { return this.form.controls; }

  onSubmit() {
    this.submitted = true;
    if (this.form.invalid) {
      return;
    }

    this.loading = true;
    console.log("toktok", this.token);
    this.authServices.resetPassword(this.token, this.f.password.value, this.f.confirmPassword.value)
      .pipe(first())
      .subscribe({
        next: () => {
          this.snackbarService.openSnackBar(" Password reset successful ", 5000, "center", "top", 'accepted');
          this.router.navigate(['../login'], { relativeTo: this.route });
        },
        error: error => {
          console.log(error)
          this.snackbarService.openSnackBar("Invalid Token or " + error.error.message, 5000, "center", "top", 'warning');
          this.loading = false;
        }
      });
  }
}
