import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdminPanelService } from '../../services/admin-panel.service';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { FileData } from './../../models/file';

import {environment} from 'src/environments/environment.prod'


@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {
  filedata: FileData;
  file: any;
  url = environment.apiUrl+'file/upload';

  addPeopleForm: FormGroup;
  addNewsForm: FormGroup;
  addPublicationForm: FormGroup;

  constructor(private adminPanelService: AdminPanelService,
    private http: HttpClient) {
  }

  ngOnInit(): any {
    this.initAddPublicationForm();
    this.initAddNewsForm();
    this.filedata = new FileData();

  }

  initAddPublicationForm(): any {
    this.addPublicationForm = new FormGroup({
      paperTitle: new FormControl('', Validators.required),
      authorName: new FormControl('', Validators.required),
      details: new FormControl('', Validators.required),
      file: new FormControl('', Validators.required),
    });
  }

  initAddNewsForm(): any {
    this.addNewsForm = new FormGroup({
      newsTitle: new FormControl('', Validators.required),
      newsBody: new FormControl('', Validators.required),
    });
  }

  addNewsSubmit(): any {
    console.log(this.addNewsForm.value);
    this.addNews(this.addNewsForm.value);
    Swal.fire('News Successfuly Added', 'Your news added succesfully!', 'success');
    this.addNewsForm.reset();
  }

  addPublicationSubmit(): any {
    console.log(this.addPublicationForm.value);
  }



  addNews(model: any): any {
    console.log("add news called")
    this.http.post('http://banglasketch-test.azurewebsites.net/News/insert',
      {
        "title": model.newsTitle,
        "details": model.newsBody
      })
      .subscribe(response => {
        console.log("News created");

      });
  }
  onChangeFile(event): any {
    console.log(event)
    this.file = event.srcElement.files;
    console.log(this.file)
  }
  getHttpFileOptions(token: String): object {
    return {
      observe: 'response',
      responseType: 'json',
      headers: new HttpHeaders({
        "Accept": "application/json",
        'Authorization': 'Bearer ' + token
      })
    };
  }
  submit(): any {
    let formData: FormData = new FormData();
    formData.append('PaperTitle', this.filedata.paperTitle + '');
    formData.append('authorName', this.filedata.authorName + '');
    formData.append('details', this.filedata.details + '');
    formData.append('source', this.filedata.source + '');
    formData.append('location', this.filedata.location + '');
    formData.append('date', this.filedata.date + '');
    formData.append('file', this.file[0], this.file[0].name);
    // use service call api
    // declare your httpClient
    this.http.post(this.url, formData,
      this.getHttpFileOptions(localStorage.getItem('token')))
      .subscribe(response => {
        this.addPublicationForm.reset();
        Swal.fire('File Successfuly Added', 'Your file added succesfully!', 'success')
        console.log(response);
      },
        error => {
          Swal.fire('File add failed', 'Failed', 'error')
          console.log(error);
        });
  }

}
