import { SnackbarServices } from './../../../services/utilities/snackbar-services';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthServices } from './../../../services/auth/auth-services';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  form: FormGroup;
  model: any = {};
  hide = true;
  constructor(private authService: AuthServices, private snackbarService: SnackbarServices, private router: Router) { }

  ngOnInit(): void {
    this.form = new FormGroup({
      name: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
    });
  }

  public hasError = (controlName: string, errorName: string) => {
    return this.form.controls[controlName].hasError(errorName);
  }
  login() {
    this.authService.login(this.model).subscribe(next => {
      this.snackbarService.openSnackBar(" Login Succeeded ", 5000, "center", "top", 'accepted');
      console.log("success" + next);
      this.router.navigate(['/home']);
    }, error => {
      this.snackbarService.openSnackBar(" Login Failed, " + error.error.message, 5000, "center", "top", 'warning');
    })

  }
  isloggedIn() {
    return this.authService.isLoggedIn();
  }
}
