import { SnackbarServices } from '../../../services/utilities/snackbar-services';
import { AuthServices } from './../../../services/auth/auth-services';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  Roles: any = ['Student', 'Teacher', 'Visitor'];
  selectedRoles: any;
  selectedType: any;
  form: FormGroup;
  hide: boolean = true;
  model: any = {};

  constructor(private authServices: AuthServices, private snackbarService: SnackbarServices, private router: Router) { }

  ngOnInit(): void {
    this.form = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.maxLength(20)]),
      regNumber: new FormControl('', [Validators.required, Validators.maxLength(10), Validators.pattern("^[0-9]*$"),]),
      email: new FormControl('', [Validators.required, Validators.pattern("^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$")]),
      password: new FormControl('', [Validators.required])
    });
  }
  public hasError = (controlName: string, errorName: string) => {
    return this.form.controls[controlName].hasError(errorName);
  }
  selectedRolesChange($event: any) {
    this.selectedRoles = $event;
    this.model.role = this.selectedRoles;
  }
  register() {
    this.authServices.register(this.model).subscribe(() => {
      this.snackbarService.openSnackBar(" Account Successfully Registered ", 5000, "center", "top", 'accepted');
      this.router.navigate(['/login']);
    }, err => {


      if (err.error.title) {
        console.log(err.error)
        this.snackbarService.openSnackBar(err.error.title + " Please Fill Up Required All Information", 5000, "center", "top", 'warning');
      }
      else if (err.error) {
        console.log(err.error)
        this.snackbarService.openSnackBar(err.error.message, 5000, "center", "top", 'warning');
      }
      console.log("reg nosto", err);
    })
  }

}
