import { Blocker } from './blockers/blocker/blocker';
import { UserProfileComponent } from './Components/user-profile/user-profile.component';
import { SignUpComponent } from './Components/auth/sign-up/sign-up.component';
import { LoginComponent } from './Components/auth/login/login.component';
import { SuParaComponent } from './Components/su-para/su-para-main/su-para-main.component';
import { PeopleComponent } from './Components/people/people.component';
import { HomeComponentsComponent } from './Components/home/home-components/home-components.component';
import { AboutUsComponentComponent } from './Components/about/about-us-component/about-us-component.component';
import { AdminComponent } from './Components/admin/admin.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { NewsComponent } from './Components/news/news.component';
import { NewsDetailComponent } from './Components/news/news-detail/news-detail.component';
import { PublicationsComponent } from './Components/publications/publications.component';
import { ResourceMainComponent } from './Components/resource/resource-main/resource-main.component';
import { PeopleDetailsComponent } from './Components/people-details/people-details.component';
import { PeopleUpdateComponent } from './Components/people-update/people-update.component';
import { TranslationAPIComponent } from "./Components/translation-api/translation-api.component";
import { ResetPasswordComponent } from './Components/auth/reset-password/reset-password.component';
import { SetResetPasswordComponent } from './Components/auth/set-reset-password/set-reset-password.component';
import { ChangePasswordComponent } from './Components/auth/change-password/change-password.component';
import { AdminBlockerService } from './blockers/blocker/admin-blocker.service';
import { UpdatePublicationsComponent } from './Components/publications/update-publications/update-publications.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'aboutus', component: AboutUsComponentComponent },
  { path: 'home', component: HomeComponentsComponent },
  { path: 'people', component: PeopleComponent },
  { path: 'people/:id', component: PeopleDetailsComponent },
  { path: 'user/edit', component: PeopleUpdateComponent, canActivate: [Blocker] },
  { path: 'news', component: NewsComponent },
  { path: 'news-detail', component: NewsDetailComponent },
  { path: 'suPara', component: SuParaComponent, },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: SignUpComponent },
  { path: 'reset-password', component: ResetPasswordComponent },
  { path: 'admin', component: AdminComponent, canActivate: [AdminBlockerService] },
  { path: 'api', component: TranslationAPIComponent },
  { path: 'resource', component: ResourceMainComponent },
  { path: 'publications', component: PublicationsComponent },
  { path: 'update-publications/:id', component: UpdatePublicationsComponent },
  { path: 'profile', component: UserProfileComponent },
  { path: 'profile', component: UserProfileComponent },
  { path: 'set-reset-password', component: SetResetPasswordComponent },
  { path: 'change-password', component: ChangePasswordComponent, canActivate: [Blocker] },
  { path: '**', redirectTo: '/home', pathMatch: 'full' },

];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }
