import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthServices } from 'src/app/services/auth/auth-services';
import { SnackbarServices } from 'src/app/services/utilities/snackbar-services';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
  form: FormGroup;
  model: any = {};
  hide = true;
  isEmailSent: boolean = false;
  constructor(private snackbarService: SnackbarServices, private authService: AuthServices) { }

  ngOnInit() {
    this.form = new FormGroup({
      email: new FormControl('', [Validators.required]),
    });
  }
  public hasError = (controlName: string, errorName: string) => {
    return this.form.controls[controlName].hasError(errorName);
  }
  resetPassword(email: string) {
    this.authService.reset_password(email).subscribe(next => {
      this.isEmailSent = true;
      this.snackbarService.openSnackBar(" Email Sent Succeeded ", 5000, "center", "top", 'accepted');
      console.log("success" + next);
    }, error => {
      console.log(error)
      this.snackbarService.openSnackBar(" Email Sent Failed, " + error.message, 5000, "center", "top", 'warning');
    })
  }

}
