import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdminPanelService } from '../../services/admin-panel.service';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { NewsModel } from "./news.model";
import { NewsService } from "../../services/news.service";
import { Router } from "@angular/router";
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.scss']
})
export class NewsComponent implements OnInit {
  deleteNewsUrl = 'http://banglasketch-test.azurewebsites.net/News/delete/';
  newsList: any;
  status: string;

  constructor(private http: HttpClient,
    private newsService: NewsService,
    private router: Router,
    @Inject(DOCUMENT) private _document: Document) { }

  // tslint:disable-next-line:typedef
  ngOnInit() {
    this.getNews();
  }

  getNews(): void {
    this.http.get('http://banglasketch-test.azurewebsites.net/News/')
      .subscribe(response => {
        this.newsList = response;
        console.log(this.newsList);
      });
  }

  onReadMore(news: NewsModel): void {
    this.newsService.setNewsDetails({
      title: news.title,
      details: news.details
    });
    this.router.navigate(['news-detail']);
  }

  getCookie(name) {
    var pattern = RegExp(name + "=.[^;]*")
    var matched = document.cookie.match(pattern)
    if (matched) {
      var cookie = matched[0].split('=')
      return cookie[1]
    }
    return false
  }

  isAdmin(): boolean {
    if (this.getCookie('isAdmin') === 'true') return true;
    return false;
  }

  deleteNews(id: string) {
    this.http.delete(this.deleteNewsUrl + id)
      .subscribe(() => this.status = 'Delete successful');
    Swal.fire('File Successfuly Deleted', 'Your file deleted succesfully!', 'success')
    console.log("fired")
    console.log(id)
    this._document.defaultView.location.reload();
  }

  // onDelete(item: any) {
  //   this.http.delete(`http://localhost:5000/News/delete/'${item.Id}'`)
  //     .subscribe(response => {
  //       console.log(response);
  //     });
  // }
}
