import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators'
import { Injectable, OnInit } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Observable } from "rxjs";
import {environment} from 'src/environments/environment.prod'

@Injectable({
  providedIn: 'root'
})

export class TranslationService implements OnInit {
  url = "http://35.220.134.57/";
  saveURL = environment.apiUrl+"ApiSave/ApiPost";
  constructor(private http: HttpClient) { }

  ngOnInit(): void {

  }

  getTranslatedText(text: any): Observable<any> {
    return this.http.post(this.url + 'translate', {
      segments: text
    }).pipe(
      map((response: any) => {
        return response;
      }));
  }

  saveTranslatedText(engText: string, bngText: string): Observable<any> {
    return this.http.post(this.saveURL, {
      EnglishText: engText,
      BanglaText: bngText
    }).pipe(
      map((response: any) => {
        console.log("saved successfully");
        return response;
      }));
  }
}
